#rcDialogTitle0
{
	text-align: center;
	color: #fff;
}
.uploadModal  .ant-modal-header
{
	border: none;
	background: none;
}
.uploadModal .ant-modal-content
{
	background-image: url(yangshi_bg.png);
}
.uploadModal .ant-modal-footer
{
	border: none;
}
.uploadModal .ant-modal-footer
{
	text-align: left;
}
.uploadModal .ant-btn:first-child
{
	background-color: #212ac5;
	color: #00a2ff;
	border-radius: 20px;
	border: 1px solid #00a2ff;
}
.uploadModal .ant-btn-primary
{
	margin-left: 25rem;
	background-color: #0168b7;
	border-radius: 20px;
	color: #fff;
}
.uploadName span:first-child
{
	color: #29cefe;
}
.uploadName span:last-child
{
	color: #fff;
}
.styleContainer
{
	float: left;
}
.uploadStyle
{
	overflow: hidden;
	width: 10000px;
}
.uploadModal .ant-modal-body
{
	overflow: hidden;
}
.styleContainer 
{
	margin-left: 0.2rem;
}
.uploadModal .ant-modal-body
{
	margin-left: 40px;
	margin-right: 40px;
	padding-left: 0px;
	padding-right: 0px;
}
.styleLeft
{
	position: absolute;
	background-image: url(yangshi_zuo.png);
	width: 1rem;
	height: 3.8rem;
	display: inline-block;
	z-index: 999;
	top:40%;
	left: 15px;
}
.styleLeft:active
{
	background-image: url(yangshi_zuo_anxia.png);
}
.styleRight
{
	position: absolute;
	background-image: url(yangshi_you.png);
	width: 1rem;
	height: 3.8rem;
	display: inline-block;
	z-index: 999;
	top:40%;
	right: 15px;
}
.styleRight:active
{
	background-image: url(yangshi_you_anxia.png);
}
