.layerGroup div
{
	width: 7.3rem;
	height: 1.875rem;
	float: left;
	text-align: center;
	border: 1px solid #2780f8;
	line-height: 1.875rem;
	color: #2780f8;
	font-weight: bold;
	font-size: 1rem;
	margin-top: 20px;
	cursor: pointer;
}
.layerGroup
{
	overflow: hidden;
}
.layerVect
{
	border-left: none!important;
	border-right: none!important;
}
.layerIMG
{
	border-radius: 5px 0 0 5px;
}
.layerPar
{
	border-radius: 0px 5px 5px 0px;
}
.ant-modal-body
{
	overflow-y: hidden;
}
.layerLine
{
	height: 1rem;
	width: 100%;
	background-color: #eee;
	margin-top: 1rem;
}
.layerTitle
{
	width: 100%;
	padding-left: 15px;
	box-sizing: border-box;
	border-bottom: 1px solid #d6d6d6;
	height: 2rem;
	line-height: 2rem;
	font-size: 1rem;
}
.layerData
{
	height: 2rem;
	border-bottom: 1px solid #d6d6d6;
	margin-left: 2rem;
	line-height: 2rem;
	
}
.layerModal .ant-modal-body
{
	padding-right: 0px;
	padding-left: 0px;
}