#swipe{
    display: none;
    position: absolute;
    width: 60%;
    bottom: 10px;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    
}
.startCompare
{
    display: block;
    font-size: 3.75rem;
    /* position: absolute;
    top: 1.25rem;
    left: 5.25rem; */
    width: 3.75rem;
    height: 3.75rem;
}
.compareList span
{
    display: block;
    height: 30px;
    line-height: 30px;
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.65);
    padding-left: 10px;
    box-sizing: border-box;
    color: #e6e3e3;
}
.compareList div
{
    border-bottom: 1px rgba(0, 0, 0, 0.65) solid;
    height: 25px;
    line-height: 25px;
    cursor: pointer;
}
.left
{
    float: left;
    width: 30%;
}
.left img{
    cursor: pointer;
}
.comLayer
{
    width: 40%;
    max-height: 200px;
    overflow-y: auto;
}
.comTop
{
    min-height: 200px;
    border: 1px solid #000;
}
.comBottom
{
    min-height: 200px;
    border: 1px solid #000;
}
.compareName{
    display: inline-block;
    width: 30%;
    text-align: center;
    font-weight: bold;
}
.compareNameC{
    width: 40%;
}